import request from '@/utils/request.js';

const tagHistoryApi = {
    /**
     * 获取设备列表
     * @param {*} params 
     */
    getTagHistorys(params) {
        return request.get('api/tags-history/list?'+params);
    },

    downloadTagHistorys(params) {
        return request.get('api/tags-history/exportExcel?'+params);
    },
}
export default tagHistoryApi;


